:root {
  --font-body: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --red: hsl(0, 100%, 74%);
  --green: hsl(154, 59%, 51%);
  --blue: hsl(248, 32%, 49%);
  --darkblue: hsl(249, 10%, 26%);
  --grayishblue: hsl(246, 25%, 77%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background-image: url(/app/images/bg-intro-desktop.png);
  background-position: center;
  background-repeat: repeat-y;
  background-size: cover;
  background-color: var(--red);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 90rem;
  height: 100vh;
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

.intro {
  display: grid;
  grid-template-columns: repeat(2, 600px);
  gap: 2rem;
}
.intro-headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--white);
}
.intro-headline h1 {
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
  margin-top: 14rem;
  margin-left: 4rem;
  padding: 0;
}
.intro-headline h6 {
  font-weight: 400;
  font-size: 1rem;
  margin: 0;
  margin-top: 2rem;
  margin-left: 4rem;
  padding: 0;
}
.intro-form .free-trial {
  background-color: var(--blue);
  color: var(--white);
  text-align: center;
  padding: 1rem;
  border-radius: 5px;
  margin: 2rem;
  border: none;
  box-shadow: 0 10px 10px var(--darkblue);
}
.intro-form form {
  background-color: var(--white);
  padding: 2rem 0;
  border-radius: 5px;
  box-shadow: 0 10px 10px var(--darkblue);
  margin: 0 2rem;
}
.intro-form input {
  display: block;
  width: 90%;
  padding: 1rem 2rem;
  margin: 1rem auto;
  border-radius: 5px;
  border: 1px solid var(--grayishblue);
  font-weight: 600;
  font-size: 1rem;
}
.intro-form input:focus {
  outline: 1px solid var(--darkblue);
  border: none;
}
.intro-form input.error {
  border: 2px solid var(--red);
  color: var(--red);
  outline: none;
}
.intro-form label {
  margin: 0;
  padding: 0;
  position: relative;
  left: 346px;
  top: -12px;
  font-style: italic;
  color: var(--red);
  font-size: 0.7rem;
}
.intro-form img {
  position: relative;
  top: -53px;
  left: 300px;
}
.intro-form button {
  display: block;
  width: 90%;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
  background-color: var(--green);
  color: var(--white);
  text-align: center;
  padding: 1rem;
  border-radius: 5px;
  margin: 1rem auto;
  border: none;
  margin-bottom: 0;
  box-shadow: 0 5px 5px var(--green);
  cursor: pointer;
}
.intro-form button:hover {
  opacity: 0.7;
}
.intro-form p {
  text-align: center;
  color: var(--grayishblue);
  font-size: 0.7rem;
}
.intro-form p span {
  color: var(--red);
}